.main-header__nav {
  padding-top: 17px;
  padding-bottom: 17px;
  margin-left: 245px;
}
@media (min-width: 768px) {
  .main-header__nav {
    padding-top: 0;
    padding-bottom: 0;
    margin-left: 0;
  }
}
@media (min-width: 1280px) {
  .main-header__nav {
    padding-top: 0;
    padding-bottom: 0;
    margin-left: 0;
  }
}
.main-header {
  position: relative;
  width: 320px;
  display: flex;
  align-items: center;
  margin: 0 auto;
  flex-direction: row;
  justify-content: center;
}
.main-header .main-header__logo {
  position: absolute;
  z-index: 5;
  top: 0;
}
@media (min-width: 768px) {
  .main-header .main-header__logo {
    display: none;
  }
}
@media (min-width: 1280px) {
  .main-header .main-header__logo {
    display: none;
  }
  .main-header .main-header__nav {
    padding-top: 0;
    padding-bottom: 0;
    margin-left: 0;
  }
}
